// Using css variables

:root {
  --clr-dark: hsl(241, 86%, 17%);
  --clr-light: hsl(240, 81%, 96%);
  --clr-secondary: hsl(260, 3, 34);
  --clr-warning: hsl(15, 94, 54);
  --clr-danger: hsl(9, 100, 43);
  --clr-primary: rgb(5, 223, 23);
  --clr-darkest: hsl(240, 82%, 4%);
  --clr-link: rgb(6, 69, 173);
  --clr-link_visited: hsl(221, 88%, 58%);
  ---clr-headings:-clr-headings: #222;
  --clr-code_snippets: hsl(240, 82%, 4%);
  --clr-code_snippets_bg: hsl(0, 0%, 97%);
  --pm: #0fa0ce; /* link effect */
  --bd: #e1e1e1; /* border */
  --bg: #fff; /* Background */
 
  --ft: #222; /* Font */
  --tag: #333; /* Tag Font */

}

@media (prefers-color-scheme: dark) {
  :root {
    --bg: rgb(18, 18, 18);
    --ft: rgb(238, 238, 238);
    --clr-headings: hsl(113, 100%, 30%);
    --bd: #555;
    --tag: #999;
    --clr-code_snippets: rgb(200, 199, 199);
    --clr-code_snippets_bg: #414141;
    --clr-link: #498bf7;
    --clr-link_visited: hsl(306, 34%, 63%);
    img {
      filter: brightness(0.8) contrast(1.2);
    }
    body {
      font-weight: 350;
    }
  }
}
