.md blockquote {
  // background-color: rgba(148, 148, 149, 0.08) ;
  // margin: 1em 0px;
  padding: 1.1em 20px 1px 20px;
  // padding: 0 2em;
  // border-left: 8px solid var(--pm);
  position: relative;
}

.md blockquote p {
  font-weight: 700;
  font-size: 1.8rem;
  line-height: 2rem;
  font-family: var(--font-baskerville);
}

blockquote:before, blockquote:after{
  position: absolute;
  font-size: 3rem;
}

.md blockquote:before {
  content: "\201C";
  left: -0.1em;
  top: 0.7em;
}
//  .md blockquote:after {
//    content: "\201D";
//    right: 0;
//    bottom: 0em;
//  }

.md table {
  border-collapse: collapse;
  border-spacing: 0;
  display: block;
  max-width: 100%;
  width: 100%;
  margin: 1em 0;
  overflow: auto;
}

.md table th {
  background-color: var(--bg);
}

.md table th, table td {
  padding: 10px 20px;
  border: 1px solid var(--bd);
}

.md img {
  // width: 100%;
  max-width: 100%;
}

.md li {
  margin-top: .5em;
}

.md > .highlight {
  max-width: 100%;
  font-size: 1.1rem;
  line-height: 1.1rem;
}
