@use '../util' as *;

html {
  box-sizing: border-box;
  
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  max-width:30em;
	margin:0 auto;
  padding: 5px;
  color: var(--ft);
  font-family: var(--font-georgia);
  font-size: clamp(1rem, 0.8750rem + 1.1250vw, 1.4rem);
    // https://css-tricks.com/linearly-scale-font-size-with-css-clamp-based-on-the-viewport/
  line-height: clamp(1.4rem, -0.1750rem + 7.0000vw, 3.5rem);
}

