@charset "UTF-8";
/*
h1 {
    font-size: rem(30);
}
*/
:root {
  --font-crimson: "Crimson Text", serif;
  --font-garamond: "EB Garamond", serif;
  --font-merriweather: "Merriweather", serif;
  --font-georgia: "Georgia", serif;
  --font-baskerville: "Libre Baskerville", serif;
  --font-pre: Consolas, "Andale Mono WT", "Andale Mono", Menlo, Monaco, "Lucida Console", "Lucida Sans Typewriter", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Liberation Mono", "Nimbus Mono L", "Courier New", Courier, "YaHei Consolas Hybrid", monospace, "Segoe UI Emoji", "PingFang SC", "Microsoft YaHei";
}

:root {
  --clr-dark: hsl(241, 86%, 17%);
  --clr-light: hsl(240, 81%, 96%);
  --clr-secondary: hsl(260, 3, 34);
  --clr-warning: hsl(15, 94, 54);
  --clr-danger: hsl(9, 100, 43);
  --clr-primary: rgb(5, 223, 23);
  --clr-darkest: hsl(240, 82%, 4%);
  --clr-link: rgb(6, 69, 173);
  --clr-link_visited: hsl(221, 88%, 58%);
  ---clr-headings:-clr-headings: #222;
  --clr-code_snippets: hsl(240, 82%, 4%);
  --clr-code_snippets_bg: hsl(0, 0%, 97%);
  --pm: #0fa0ce;
  /* link effect */
  --bd: #e1e1e1;
  /* border */
  --bg: #fff;
  /* Background */
  --ft: #222;
  /* Font */
  --tag: #333;
  /* Tag Font */
}

@media (prefers-color-scheme: dark) {
  :root {
    --bg: rgb(18, 18, 18);
    --ft: rgb(238, 238, 238);
    --clr-headings: hsl(113, 100%, 30%);
    --bd: #555;
    --tag: #999;
    --clr-code_snippets: rgb(200, 199, 199);
    --clr-code_snippets_bg: #414141;
    --clr-link: #498bf7;
    --clr-link_visited: hsl(306, 34%, 63%);
  }
  :root img {
    filter: brightness(0.8) contrast(1.2);
  }
  :root body {
    font-weight: 350;
  }
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  max-width: 30em;
  margin: 0 auto;
  padding: 5px;
  color: var(--ft);
  font-family: var(--font-georgia);
  font-size: clamp(1rem, 0.8750rem + 1.1250vw, 1.4rem);
  line-height: clamp(1.4rem, -0.1750rem + 7.0000vw, 3.5rem);
}

*,
*::after,
::before {
  margin-top: 0;
}

* + * {
  margin-top: 0em;
  margin-bottom: 0em;
}

p + p,
li + li {
  margin-top: 1rem;
}

h1,
h2,
h3,
h4,
dt,
small,
.small,
dl#tweetlist dt,
footer,
label,
cite,
div.blogparent,
input[type=submit] {
  font-family: var(--font-baskerville);
  font-weight: normal;
  font-style: normal;
}

h1 {
  margin-top: 0.5rem;
  font-size: 2em;
  line-height: 1em;
  margin-bottom: 1.5em;
}

a {
  text-decoration: none;
  color: var(--clr-link);
  overflow-wrap: break-word;
  word-wrap: break-word;
}

a:visited {
  color: var(--clr-link);
}

a:hover {
  text-decoration: underline;
}

a:not(.icon)[href^=http]::after {
  content: url(/images/external.svg);
}

@media (prefers-color-scheme: dark) {
  a:not(.icon)[href^=http]::after {
    content: url(/images/external_dark.svg);
  }
}
.postdate {
  font-size: 1.2rem;
  line-height: 1.8rem;
}

.read-more {
  font-size: 0.8em;
}

h1,
h1 > a,
h1 a:visited,
h1 > a:active,
h1 > a:hover {
  color: var(--clr-headings);
}

h2 {
  color: var(--clr-headings);
}

h2 a {
  color: var(--clr-headings);
  text-decoration: none;
}

p {
  line-height: 1.8rem;
  text-align: left;
}

p, dl {
  display: block;
  margin-block-start: 1rem;
  margin-block-end: 1rem;
}

p > code,
kbd,
samp {
  font-family: var(--font-pre);
  /* 1 */
  font-size: 0.9em;
  /* 2 */
  color: var(--clr-code_snippets);
  background-color: var(--clr-code_snippets_bg);
  padding: 2px 4px;
  border-radius: 3px;
}

pre {
  font-family: var(--font-pre);
  /* 1 */
  font-size: 0.9em;
  /* 2 */
}

.highlight {
  position: relative;
}

.highlight pre {
  padding-right: 5px;
}

.highlight-copy-btn {
  position: absolute;
  bottom: 7px;
  right: 7px;
  border: 0;
  border-radius: 4px;
  padding: 1px;
  font-size: 0.7em;
  line-height: 1.8;
  color: #fff;
  background-color: #777;
  opacity: 0.6;
  min-width: 55px;
  text-align: center;
}

.highlight-copy-btn:hover {
  background-color: #666;
}

p, dt, dl {
  max-width: 50ch;
}

main {
  min-height: calc(100vh - 10em);
}

footer {
  height: 2em;
}

.post-info {
  color: var(--ft);
  margin-top: 0em;
  font-size: clamp(0.75rem,.575rem + 1.125vw,1rem);
  line-height: clamp(1rem,-.175rem + 7vw,1.5rem);
}

.post-info a {
  color: var(--clr-link);
}

.post-info a:hover {
  color: var(--clr-headings);
}

.post-short-list .post-info {
  margin-top: 0;
  margin-bottom: 0em;
}

.post-taxonomies {
  display: inline;
}

.post-hidden-url {
  display: none;
}

.post-hidden-author {
  display: none;
}

.post-date {
  white-space: nowrap;
}

.post-categories {
  display: inline;
  list-style-type: none;
  padding: 0;
}

.post-categories li {
  display: inline;
  margin-right: 1em;
}

.post-tags {
  display: inline;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.post-tags li {
  display: inline;
  margin-right: 1em;
}

.series-pagination {
  list-style-type: none;
  font-size: 1.2rem;
  line-height: 1.8rem;
}

.series-pagination li {
  display: inline;
  margin-right: 1em;
}

.card {
  padding: 1.25rem;
}

.toggle__button {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--toggle);
}

body {
  color: var(--ft);
  background-color: var(--bg);
  transition-property: background-color, border-color, color;
  transition-duration: 0.5s;
}

hr {
  margin: 3rem 0;
  border-width: 0;
  border-top: 1px solid var(--bd);
}

pre,
code {
  font-family: var(--font-pre);
}

.icon:hover {
  transform: translateY(-5px);
  transition: transform 0.3s ease-in-out;
}

main {
  animation: showup 0.7s;
}

.row {
  display: flex;
  align-items: center;
}

.col {
  display: flex;
  flex-direction: column;
}

.jc-bt {
  justify-content: space-between;
}

.as-s {
  align-self: start;
}

.ai-c {
  align-items: center;
}

.gap-0_5 {
  gap: 0.8rem;
}

.gap-1 {
  gap: 1rem;
  row-gap: 1rem;
}

.gap-2 {
  gap: 2rem;
  row-gap: 2rem;
}

.gap-3 {
  gap: 3rem;
  row-gap: 3rem;
}

.mtb-1 {
  margin: 1em 0;
}

.mtb-2 {
  margin: 2em 0;
}

.mb-1 {
  margin-bottom: 1em;
}

.mb-0 {
  margin-bottom: 0;
}

.mt-2 {
  margin-top: 2em;
}

.ml-1 {
  margin-left: 1em;
}

.btn {
  cursor: pointer;
}

.hidden {
  display: none;
}

.tag-pm {
  font-size: 0.7em;
  font-weight: bold;
  line-height: 1;
  color: #fff;
  background-color: var(--pm);
  border-radius: 20px;
  padding: 2px 8px;
}

.footer-small-text {
  font-size: 0.7rem;
  line-height: 1;
}

@keyframes showup {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: none;
  }
}
@media (max-width: 640px) {
  .container {
    width: 90%;
  }

  .row-mob {
    flex-direction: column;
    align-items: flex-start;
    gap: 0;
    row-gap: 0;
  }

  .al-c-mob {
    align-items: center;
  }

  .col-rev-mob {
    flex-direction: column-reverse;
  }

  .mb-0_5-mob {
    margin-bottom: 0.5em;
  }

  .mb-1_5-mob {
    margin-bottom: 1.5em;
  }
}
/* Scroll bar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-thumb {
  background: rgba(128, 128, 128, 0.7);
}

::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(128, 128, 128, 0.2);
}

::-webkit-scrollbar-thumb:active {
  background-color: gray;
}

pre[class*=language-],
code[class*=language-] {
  color: #d4d4d4;
  font-size: 1rem;
  text-shadow: none;
  font-family: var(--ft);
  line-height: 1.5;
  color: #d4d4d4;
}

.chroma {
  background-color: #292929;
  margin: 0.5em 0;
}

/* LineTableTD */
.chroma .lntd {
  vertical-align: top;
  padding: 0;
  margin: 0;
  border: 0;
  width: 2em;
}

.chroma .lntable {
  border-spacing: 0;
  padding: 0;
  margin: 0;
  border: 0;
  width: auto;
  overflow: auto;
  display: block;
}

/* LineNumbersTable */
.chroma .lnt {
  color: #666;
  padding-left: 0.5em;
  padding-right: 1em;
  text-align: right;
  line-height: 1.5;
  font-size: 1rem;
}

/* Keyword */
.chroma .kc,
.chroma .kd,
.chroma .kn,
.chroma .kp,
.chroma .kr,
.chroma .kt,
.chroma .k,
.chroma .si {
  color: #569cd6;
}

.chroma .na {
  color: #9cdcfe;
}

.chroma .n,
.chroma .nb,
.chroma .bp,
.chroma .nc,
.chroma .no,
.chroma .nd,
.chroma .ni,
.chroma .ne,
.chroma .nf,
.chroma .fm,
.chroma .nl,
.chroma .nn {
  color: #1fd522;
}

/* NameProperty */
.chroma .py,
.chroma .nt,
.chroma .nv,
.chroma .vc,
.chroma .vg,
.chroma .vi,
.chroma .vm {
  color: #569cd6;
}

/* LiteralString */
.chroma .s,
.chroma .sa,
.chroma .sb,
.chroma .sc,
.chroma .dl,
.chroma .sd,
.chroma .s2,
.chroma .se,
.chroma .sh,
.chroma .sx,
.chroma .sr,
.chroma .s1,
.chroma .ss {
  color: #ce9178;
}

/* LiteralNumber */
.chroma .m,
.chroma .mb,
.chroma .mf,
.chroma .mh,
.chroma .mi,
.chroma .il,
.chroma .mo {
  color: #b5cea8;
}

/* Comment */
.chroma .c,
.chroma .ch,
.chroma .cm,
.chroma .c1,
.chroma .cs {
  color: #517043;
}

.md h1 {
  font-size: 2.7rem;
  line-height: 2rem;
  font-weight: 700;
  margin-bottom: 1em;
}

.md h2, h3 {
  margin-top: 1.5rem;
  margin-bottom: 1rem;
  font-weight: 700;
  line-height: 1.8rem;
}

h2,
h3 {
  margin-top: 1.5em;
  margin-bottom: 1em;
  font-weight: 700;
  line-height: 1.8rem;
}

.md h2 {
  font-size: 1.7rem;
}

.md h3 {
  font-size: 1.4rem;
  line-height: 1.5rem;
}

.md blockquote {
  padding: 1.1em 20px 1px 20px;
  position: relative;
}

.md blockquote p {
  font-weight: 700;
  font-size: 1.8rem;
  line-height: 2rem;
  font-family: var(--font-baskerville);
}

blockquote:before, blockquote:after {
  position: absolute;
  font-size: 3rem;
}

.md blockquote:before {
  content: "“";
  left: -0.1em;
  top: 0.7em;
}

.md table {
  border-collapse: collapse;
  border-spacing: 0;
  display: block;
  max-width: 100%;
  width: 100%;
  margin: 1em 0;
  overflow: auto;
}

.md table th {
  background-color: var(--bg);
}

.md table th, table td {
  padding: 10px 20px;
  border: 1px solid var(--bd);
}

.md img {
  max-width: 100%;
}

.md li {
  margin-top: 0.5em;
}

.md > .highlight {
  max-width: 100%;
  font-size: 1.1rem;
  line-height: 1.1rem;
}