
body {
  color: var(--ft);
  background-color: var(--bg);
  transition-property: background-color, border-color, color;
  transition-duration: .5s;
}

hr {
  margin: 3rem 0;
  border-width: 0;
  border-top: 1px solid var(--bd);
}

pre,
code {
  font-family: var(--font-pre);
}


.icon:hover {
  transform: translateY(-5px);
  transition: transform .3s ease-in-out;
}

main {
  animation: showup 0.7s;
}

.row {
  display: flex;
  align-items: center;
}

.col {
  display: flex;
  flex-direction: column;
}

.jc-bt {
  justify-content: space-between;
}

.as-s {
  align-self: start;
}

.ai-c {
  align-items: center;
}

.gap-0_5 {
  gap: .8rem;
}

.gap-1 {
  gap: 1rem;
  row-gap: 1rem;
}

.gap-2 {
  gap: 2rem;
  row-gap: 2rem;
}

.gap-3 {
  gap: 3rem;
  row-gap: 3rem;
}

.mtb-1 {
  margin: 1em 0;
}

.mtb-2 {
  margin: 2em 0;
}

.mb-1 {
  margin-bottom: 1em;
}

.mb-0 {
  margin-bottom: 0;
}

.mt-2 {
  margin-top: 2em;
}

.ml-1 {
  margin-left: 1em;
}

.btn {
  cursor: pointer;
}

.hidden {
  display: none;
}

.tag-pm {
  font-size: .7em;
  font-weight: bold;
  line-height: 1;
  color: #fff;
  background-color: var(--pm);
  border-radius: 20px;
  padding: 2px 8px;
}

.footer-small-text {
  font-size: .7rem;
  line-height: 1;
}

@keyframes showup {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

@media (max-width: 640px) {
  .container {
    width: 90%;
  }

  .row-mob {
    flex-direction: column;
    align-items: flex-start;
    gap: 0;
    row-gap: 0;
  }

  .al-c-mob {
    align-items: center;
  }

  .col-rev-mob {
    flex-direction: column-reverse;
  }


  .mb-0_5-mob {
    margin-bottom: .5em;
  }

  .mb-1_5-mob {
    margin-bottom: 1.5em;
  }
}

/* Scroll bar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-thumb {
  background: rgb(128 128 128 / 70%);
}
::-webkit-scrollbar-thumb:window-inactive {
  background: rgb(128 128 128 / 20%);
}

::-webkit-scrollbar-thumb:active {
  background-color: rgb(128 128 128 / 100%);
}