// Canalized from Anubis Theme for post list pages

.post-info {
    color: var(--ft);
    // font-size: 0.75em;
    margin-top: 0em;
    font-size: clamp(0.75rem,.575rem + 1.125vw,1rem);
    line-height: clamp(1rem,-.175rem + 7vw,1.5rem);
}

.post-info a {
    color: var(--clr-link);
}

.post-info a:hover {
    color: var(--clr-headings);
}

.post-short-list .post-info {
    margin-top: 0;
    margin-bottom: 0em;
}

.post-taxonomies {
    display: inline;
}

.post-hidden-url {
    display: none;
}

.post-hidden-author {
    display: none;
}

.post-date {
    white-space: nowrap;
}

.post-categories {
    display: inline;
    list-style-type: none;
    padding: 0;
}

.post-categories li {
    display: inline;
    margin-right: 1em;
}

.post-tags {
    display: inline;
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.post-tags li {
    display: inline;
    margin-right: 1em;
}
